
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import Icon from 'vue-awesome/components/Icon.vue'

export default {
name: 'AppFooter',
props: {
tabItems: {
type: Array,
default: null
}
},
components: {
Tabbar,
TabbarItem,
Icon
},
data () {
return {
}
},
watch: {
'$route': function (newV) { // 保持tabitem的选择状态和path一致
let activeIndex = this.tabItems.findIndex(i => i.path === newV.path)
if (activeIndex >= 0) {
for (let i = 0; i < this.tabItems.length; i++) {
if (activeIndex === i) {
this.tabItems[i].actived = true
} else {
this.tabItems[i].actived = false
}
}
}
}
},
methods: {
tabChange (val) {
this.$router.push({ path: this.tabItems[val].path })
}
}
}



